import React, { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import { BrowserRouter as Router, Routes, Route, Link } from "react-router-dom";
import axios from "axios";
import "./Header.css";
function Header() {
  const [user, setUser] = useState({});
  const [logStatus, setLogStatus] = useState(false);
  const [toggle, setToggle] = useState(false);
  const btnRef = useRef();
  useEffect(() => {
    if (localStorage.getItem("token")) {
      getUser();
    }
  }, []);
  const getUser = () => {
    axios
      .get(`/user`, {
        headers: {
          Authorization: localStorage.getItem("token"),
        },
      })
      .then((res) => res)
      .then(({ data }) => {
        // console.log("-------", data);
        setUser(data.data);
        setLogStatus(true);
      })
      .catch((err) => {
        setLogStatus(false);
        console.log(err);
      });
  };
  //   console.log("-------------------", btnRef);
  return (
    <Navbar>
      <Link to="/">
        <Logo src="data:image/png;base64," />
      </Link>
      <NavMenu>
        <a>
          <Link to="/">STORE</Link>
        </a>
        <a>
          <a href="https://www.epicgames.com/site/en-US/epic-games-store-faq?sessionInvalidated=true">
            FAQ
          </a>
        </a>
        <a>
          <a href="https://www.epicgames.com/help/en-US/">HELP</a>
        </a>
      </NavMenu>
      <NavTools>
        <LanguageImage src="https://img.icons8.com/ios/50/ffffff/globe--v1.png" />

        <UserImage src="https://img.icons8.com/ios/60/ffffff/user--v2.png" />

        <SignIn>
          {logStatus ? (
            <a
              style={{ userSelect: "none" }}
              onClick={() => {
                if (toggle) {
                  btnRef.current.style.display = "none";
                  setToggle(false);
                } else {
                  btnRef.current.style.display = "block";
                  setToggle(true);
                }
              }}
            >
              {user.displayName}
            </a>
          ) : (
            <Link to="/login">
              <a>SIGN IN</a>
            </Link>
          )}
        </SignIn>
        <div
          onClick={() => {
            localStorage.removeItem("token");
            window.location.reload();
          }}
          ref={btnRef}
          className="header_user_options"
        >
          <div>Logout</div>
        </div>
        <SubscribeButton>DOWNLOAD</SubscribeButton>
      </NavTools>
    </Navbar>
  );
}

export default Header;

const Navbar = styled.nav`
  width: 95.5vw;
  height: 50px;
  background: #2a2a2a;
  display: flex;
  align-items: center;
  padding-left: 1.5vw;
  padding-right: 1.5vw;
`;

const Logo = styled.img`
  width: 40px;
`;

const NavMenu = styled.div`
  display: flex;
  flex: 1;
  margin-left: 20px;
  align-items: center;
  font-weight: 500;
  gap: 5px;

  a {
    display: flex;
    align-items: center;
    padding: 0 14px;
    cursor: pointer;
    color: #d5d5d5;
    font-size: 12px;
    text-decoration: none;
  }
  span {
    font-size: 10px;
    letter-spacing: 0.5px;
    color: #d5d5d5;
  }
`;

const NavTools = styled.div`
  display: flex;
  margin-left: 50px;
  align-items: center;
  gap: 28px;
`;

const SearchImage = styled.img`
  cursor: pointer;
`;
const LanguageImage = styled.img`
  cursor: pointer;
  width: 22px;
  border-radius: 50%;
`;
const SubscribeButton = styled.button`
  padding: 5px 23px;
  background: blue;
  font-family: sans-serif;
  min-width: 140px;
  min-height: 40px;
  font-style: normal;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 12px;
  border: none;
  color: white;
  cursor: pointer;
`;
const UserImage = styled.img`
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background: #37474f;
`;
const SignIn = styled.span`
  font-size: 12px;
  margin-right: 15px;
  margin-left: -15px;
  letter-spacing: 1;
  cursor: pointer;
  color: #d5d5d5;

  a {
    text-decoration: none;
    color: white;
  }
`;
